  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

html {
  font-size: 62.5%;
  line-height: 1.6;
}

body {
  font-family: "Nunito", sans-serif, system-ui, "Segoe UI", Roboto, Helvetica,
    Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-weight: 500;
  background: rgb(21, 36, 95);
  background: linear-gradient(
    180deg,
    rgba(21, 36, 95, 1) 0%,
    rgba(69, 62, 131, 1) 40%,
    rgba(145, 96, 160, 1) 100%
  );
}

.app-container {
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-rows: max-content max-content;
  color: #fafafa;
  margin: 0px 50px;
  column-gap: 70px;
}

.weather-report-container {
  display: grid;
  grid-template-rows: repeat(4, max-content);
  padding: 1.5rem;
  align-content: space-between;
  margin: 20px 0px;
}

.weather-report-container,
.environmental-informations,
.hourly-weather,
.daily-weather,
.app-footer {
  backdrop-filter: blur(16px) saturate(180%);
  -webkit-backdrop-filter: blur(16px) saturate(180%);
  background-color: rgba(17, 25, 40, 0.75);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.125);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
}

.search-box {
  display: grid;
  grid-template-rows: max-content max-content;
  gap: 5px;
}

.search-box > div {
  display: flex;
  gap: 10px;
}

.search-box > div > input {
  font-family: inherit;
  font-weight: 600;
  width: 100%;
  border: none;
  border-radius: 5px;
  padding: 0.5rem 1rem;
  font-size: 1.6rem;
  text-align: center;
  background-color: #e5e5e5;
}

.search-box > div > button {
  background-color: #e5e5e5;
  border: none;
  padding: 0rem 0.5rem;
  border-radius: 5px;
}

.error-message {
  font-size: 1.6rem;
  font-weight: 600;
  font-style: italic;
  color: #ef4444;
}

ion-icon {
  display: flex;
  align-items: center;
  font-size: 1.9rem;
}

.weather-report {
  display: grid;
  gap: 10px;
}

.location-time {
  display: flex;
  justify-content: space-between;
}

.location-time > * {
  font-size: 1.8rem;
  font-weight: 700;
}

#currentWeatherText {
  font-size: 2.4rem;
  font-weight: 700;
}

.temperature {
  display: flex;
}

#currentTemp {
  font-size: 10rem;
  font-weight: 800;
}

#degree {
  margin-top: 20px;
  font-size: 2.8rem;
}

#currentWeatherIcon {
  width: 200px;
}

.temperatureMinMax {
  display: flex;
  align-items: center;
  gap: 5px;
}

.temperatureMinMax > * {
  font-size: 1.6rem;
  font-weight: 500;
}

#currentTempMax {
  font-size: 1.7rem;
  font-weight: 700;
}

.weather-conditions {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.primary-info {
  display: grid;
  grid-template-columns: max-content 1fr;
  grid-auto-rows: max-content;
  align-items: center;
}

.additional-icon {
  width: 90px;
  height: 90px;
}

.info-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.info-title {
  font-size: 1.6rem;
  color: #f5f5f5;
}

.info-value {
  font-size: 1.7rem;
  font-weight: 700;
}

.weather-additional-container {
  display: grid;
  grid-template-rows: repeat(6, max-content);
  gap: 10px;
}

.topbar {
  display: flex;
  justify-content: space-between;
  padding-top: 10px;
  align-items: center;
}

.current-date {
  font-size: 1.6rem;
  font-weight: 700;
}

.logo {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.app-name {
  font-family: "Agbalumo", system-ui;
  font-weight: 400;
  font-size: 1.8rem;
}

.environmental-informations {
  display: grid;
  grid-template-columns: repeat(3, max-content);
  grid-template-rows: repeat(2, max-content);
  padding: 0.5rem 1.5rem;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  margin-bottom: 30px;
}

.hourly-title,
.daily-title {
  font-size: 1.8rem;
  font-weight: 800;
}

.hourly-weather,
.daily-weather {
  display: flex;
  overflow-x: scroll;
  padding: 0.5rem 1.5rem;
  justify-content: space-between;
  margin-bottom: 30px;
}

.weather-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
}

.weather-info > p {
  font-size: 1.6rem;
  font-weight: 700;
  width: 90px;
  text-align: center;
}

.feelsLike-icon {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

.pop-icon {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

.sunrise-icon {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

.sunset-icon {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
}

.humidity-icon {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
}

.uv-icon {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
}

.pressure-icon {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_6___});
}

.wind-icon {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_7___});
}

.app-footer {
  grid-column: 1/3;
  display: flex;
  justify-content: space-between;
  padding: 1rem 1.5rem;
}

.profile-link {
  display: flex;
  gap: 10px;
}

a {
  text-decoration: none;
  color: #e5e5e5;
}

a > ion-icon {
  font-size: 2rem;
  color: #e5e5e5;
}

a > ion-icon:hover {
  scale: 1.05;
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-thumb {
  background-color: #d4d4d4;
  border-radius: 100px;
  margin: 0px 10px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-corner {
  background-color: transparent;
}
